@import url('https://fonts.googleapis.com/css2?family=Acme&family=Alegreya+SC:wght@400;500;700;800&family=Alegreya+Sans+SC:wght@300;400;500;700&family=Merriweather:wght@300;400;700&display=swap');

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}
:root {
  --color-bg: #070229;
  --color-bg-variant: #200bbb6e;
  --color-primary: hsl(192, 98%, 50%);
  --color-primary-varient: rgba(10, 27, 121, 0.926);
  --color-white: #fff;
  --color-light: rgba(246, 245, 245, 0.6);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-ms: 90%;
}

html {
  scroll-behavior: smooth;
}

::-webkit-scrollbar {
  display: none;
}

body {
  font-family: 'Acme', sans-serif;
  background: var(--color-bg);
  color: var(--color-white);
  line-height: 1.5;
  background-image: url(../src/Assests/bg.jpg);
}

/* aaaaaaaaaaaaaaaaaaaaaaa  general styles aaaaaaaaaaaaaaaaaaaa */

.container {
  width: var(--container-width-lg);
  margin: 0 auto;
}

h1,
h2,
h3,
h4,
h5 {
  font-weight: 500;
}

h1 {
  font-size: 2.5rem;
}

section {
  margin-top: 8rem;
}

section > h2,
section > h5 {
  text-align: center;
  color: var(--color-light);
}

section > h2 {
  color: var(--color-primary);
  margin-bottom: 3rem;
}

.text-light {
  color: var(--color-light);
}

a {
  color: var(--color-primary);
  transition: var(--transition);
}

a:hover {
  color: var(--color-white);
}
.btn {
  width: max-content;
  display: inline-block;
  color: var(--color-primary);
  padding: 0.75rem 1.2rem;
  border-radius: 0.4rem;
  cursor: pointer;
  border: 1px solid var(--color-primary);
  transition: var(--transition);
}

.btn:hover {
  background: var(--color-white);
  color: var(--color-bg);
  border-color: transparent;
}

.btn-primary {
  background: var(--color-primary);
  color: var(--color-bg);
}

img {
  display: block;
  width: 100%;
  object-fit: cover;
}

/* aaaaaaaaaaaaaa  media devices aaaaaaaaaaaaaaa */

@media screen and(max-width: 1024px) {
  .container {
    width: var(--container-width-md);
  }

  section {
    margin-top: 6rem;
  }
}

/* for mobile */

@media screen and(max-width: 600px) {
  .container {
    width: var(--container-width-ms);
  }

  section > h2 {
    margin-top: 2rem;
  }
}
